*
{
    margin:0;
    padding:0;
}
body {
    /* background: url("http://superdevresources.com/wp-content/uploads/2014/02/pattern-background.png"); */
    font-family: "Bree Serif", serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image: url('https://t4.ftcdn.net/jpg/02/67/40/21/360_F_267402109_jZvsqRQUvSxohAOmcUt549jlapqoRHM0.jpg');
    background-size: cover;
    height: 100vh;
    width: 100%;
  }
  .heading
  {
    text-align: center;
    color: #a80c5a;
    padding-bottom: 10px;
   
  
  }
  .app
  {
    background: linear-gradient(83deg,#f71aa2 0%, #6800ff 80%);
    width: 85%;
    min-height: 200px;
    height: min-content;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    display: flex;
    justify-content: space-evenly;
    background-image: url('https://img.freepik.com/free-photo/studio-background-concept-abstract-empty-light-gradient-purple-studio-room-background-product-plain-studio-background_1258-80131.jpg');
  }

.Question-section
{
position: relative;
width: 100%;
}
.Question-count
{
    color:rgb(221, 49, 192);
    font-size: 10px;
    margin-bottom: 20px;
}
.Question-count span{
    font-size: 15px;
    color:red;
}
.Question-text
{
    color: white;
    font-size: 25px;
  font-weight: bold;
  line-height: 1.5;

}
.Answer-section
{
  display: flex;
  flex-direction: column; 
  width: 100%;
  justify-content: space-between;
}
button {
    width: 100%;
    font-size: 16px;
    color: #ffffff;
    background: linear-gradient(83deg,#f71aa2 0%, #6800ff 80%);
    border-radius: 15px;
    display: flex;
    padding:10px;
    justify-content: flex-start;
    align-items: center;
    border: 3px solid #234668;
    cursor: pointer;
   
  }
  button:hover {
    background-color: #1b295c;
  }
  .score-section{
   display: flex;
  align-items: center;
  font-size: 30px;
  color:white;
  font-weight: bold;

}
@media (min-width: 0px) and (max-width: 480px)
{
  .app
  {
    margin-left: 10px;
    background: linear-gradient(83deg,#f71aa2 0%, #6800ff 80%);
    width: 85%;
    min-height: 300px;
    height: min-content;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    display: flex;
    justify-content: space-evenly;
    background-image: url('https://img.freepik.com/free-photo/studio-background-concept-abstract-empty-light-gradient-purple-studio-room-background-product-plain-studio-background_1258-80131.jpg');
  }
  .Answer-section
{
  display: flex;
  flex-direction: column; 
  width: 100%;
  justify-content: space-evenly;
  
}
.Question-text
{
    color: white;
    font-size: 20px;
  font-weight: bold;
  line-height: 1.5;

}
}